body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
  width: 100%;
}

.calc-wrapper{
  width: 400px;
  height: 600px;
}

.row{
  display: -webkit-flex;
  display: flex;
  width: 100%;
}
.title{
    height: 1.5em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-weight: bolder;
    font-size: 1.4em;
    background-color: #dd6413;
    color: #ffffff;
    -webkit-flex: 1 1;
            flex: 1 1;
    outline: 1px solid #888888;
}
.button-wrapper{
    height: 4em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-weight: lighter;
    font-size: 1.4em;
    background-color: #e0e1e6;
    color: #888888;
    -webkit-flex: 1 1;
            flex: 1 1;
    outline: 1px solid #888888;
}

.operator{
    background-color: #fe9241;
    color: #ffffff;
}
.input{
    height: 4em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    font-weight: bold;
    font-size: 1.4em;
    background-color: #000000;
    color: #ffffff;
    padding: 0.5em;
    outline: 1px solid #888888;
}
.clear-btn{
    height: 4em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    font-weight: lighter;
    font-size: 1.4em;
    background-color: #c5c3cd;
    color: #000000;
    -webkit-flex: 1 1;
            flex: 1 1;
    outline: 1px solid #888888;
}
.credit{
    height: 2em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: left;
            justify-content: left;
    -webkit-align-items: right;
            align-items: right;
    font-weight: bold;
    font-size: 0.8em;
    color: #000000;
    -webkit-flex: 1 1;
            flex: 1 1;
    outline: 1px solid #ffffff;
}
